@import '../../../styles/settings.scss';

.root {
  display: flex;
  flex-direction: column;
  padding-bottom: 70px;
  animation: opacity 1.0s ease-in-out;

  li {
    display: flex;
    align-items: flex-start;
  }
}

h2 {
  color: white;
  font-family: $menu-font;
  font-size: 40px;
  text-shadow: 2px 2px $pink;
  margin: 50px 5px;
  text-align: center;
}

.container {
  width: 80%;
  max-width: 500px;
  margin: 20px auto 0 auto;
  background-color: $pistachio;
  border-radius: 8px;
}

.avatar {
  width: 160px;
  height: 160px;
  margin-right: 10px;
}

.formContainer {
  width: 80%;
  margin: 10px auto;
}


form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

button {
  width: 100px;
  margin-top: 15px;
  background-color: $pink;
  border-radius: 8px;
  font-size: 22px;
  padding: 5px;
  color: white;
  font-family: $menu-font;
  text-shadow: 2px 2px $pink;
  border: none;
  outline: none;
  line-height: 22px;

  &:hover {
    cursor: pointer;
  }
}

.amountButton {
  width: 30px;
  margin: 0 0 0 4px;
}

.titleRemove{
  display: flex;
  justify-content: space-between;

}

.deleteButton {
  margin: 0;
  display: flex;
  text-align: center;
  justify-content: center;
}

.totalContainer h2 {
  margin: 10px 5px;
}

.needLog {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  margin-bottom: 20px;

  h4 {
    color:palevioletred;
    font-family: $menu-font;
    font-size: 17px;
  }
}

@media screen and (max-width: 450px) {
  h2 {
    font-size: 30px;
  }

  .container {
    width: 95%;
  }

  .avatar {
    width: 100px;
    height: 100px;
    margin-right: 4px;
  }

  .formContainer {
    width: 95%;
  }

  .needLog h4 {
    font-size: 15px;
  }
}

@keyframes opacity {
  1% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
